import PostProfile from './postprofile';
import FavoriteIcon from '@mui/icons-material/Favorite';
import CollectionsIcon from '@mui/icons-material/Collections';
import * as React from 'react';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import{useEffect,useState} from 'react';
import axios from 'axios';
import AddPub from '../content/AddPub'

interface TabPanelProps {
  children?: React.ReactNode;
  dir?: string;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3,backgroundColor:'#FFE5D9' }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

export default function FullWidthTabs() {
  const theme = useTheme();
  const [value, setValue] = React.useState(0);
  const[datamypub,setdatamypub]=useState([]);
  const[datamyfav,setdatamyfav]=useState([]);
  const [isloaded,setIsLoaded]=useState(false)

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index: number) => {
    setValue(index);
  };
  useEffect(()=>{
    if(!isloaded){
      const user=localStorage.getItem("user")
    axios.get(`http://127.0.0.1:8000/mypub/${user}`).then((res) =>  {
      setdatamypub(res.data)
    });
    axios.get(`http://127.0.0.1:8000/favorite/${user}`).then((res) => {
      setdatamyfav(res.data)
    });
     setIsLoaded(true)
    }
  })

  return (
    <Box sx={{ backgroundColor:'#F4ACB7', width: '100%',borderRadius:4,margin:'auto' }}>
      <AppBar position="static" sx={{backgroundColor:'#F4ACB7'}}>
        <Tabs
        sx={{backgroundColor:'#F4ACB7'}}
          value={value}
          onChange={handleChange}
          indicatorColor="secondary"
          textColor="inherit"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab icon={<CollectionsIcon />} label="My Publication" {...a11yProps(0)} />
          <Tab icon={<FavoriteIcon />}label="My Favorite" {...a11yProps(1)} />
        </Tabs>
      </AppBar>

      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <PostProfile datapub={datamypub} page={'mypub'}/>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <PostProfile datapub={datamyfav} page={'myfav'}/>
        </TabPanel>
      </SwipeableViews>
      <AddPub/>
    </Box> 
  );
}

