import { BrowserRouter as Router, Switch, Route,useHistory,Redirect } from "react-router-dom";
import React from "react";
import SignInSide from './pages/SignInSide';
import Navbar from './componant/navbar/navbar';
import Footer from './componant/footer/footer';
import Notes from './pages/Notes.js'
import NewPlan from "./pages/newPlan/NewPlan";
import Content from './pages/content';
import Profile from './pages/Profile';
import MeditateList from "./pages/MeditateList/MeditateList";
import Media from './pages/Media';
import Contact from './pages/Contact.js';
import TodoList from "./pages/ToDoList";
import Registration from './pages/Registration';
import { useEffect } from "react";
import Privacy from "./pages/Privacy";
import PlayList_list from "./pages/playlistList/PlayLists_List";
import Sidebar from "./components/sidebar/Sidebar";
import Topbar from "./components/topbar/Topbar";
import "./components/style.css";
import Account from './pages/Account'
import Home from "./pages/home/Home";
import UserList from "./pages/userList/UserList";
import User from "./pages/user/UserEdit";
import NewUser from "./pages/newUser/NewUser";
import PublicationsList from "./pages/publicationsList/PublicationsList";
import Publication from "./pages/publication/Publication";
import NewPublication from "./pages/newPublication/NewPublication";
import MessagesList from "./pages/messagesList/MessagesList";
import ReportsList from "./pages/reportsList/ReportsList";
import SongsList from "./pages/songsList/SongsList";
import Song from './pages/song/Song';
import NewSong from "./pages/newSong/NewSong";
import NewPlayList from "./pages/newPlayList/NewPlayList";
import NewMeditate from "./pages/newMeditate/NewMeditate";
import PlansList from "./pages/PlansList/PlansList";
import Plan from "./pages/plan/Plan";
import EditPlaylist from "./pages/EditPlayList/EditPlaylist";
import Meditation from "./pages/meditate/Meditation";
import EditObjective from "./pages/EditObjective";
import ForgetPassword from "./pages/ForgetPassword";
import NewPassword from './pages/NewPassword';
import ProtectedRoute from "./ProtectedRoute";
import { useState } from "react";
function Routing() {
  const history= useHistory();
  const user = localStorage.getItem("user");
  useEffect(() => {
  
    if (user==0 && 
    !history.location.pathname.startsWith("/") &&
    !history.location.pathname.startsWith("/registre") &&
    !history.location.pathname.startsWith("/resetpassword") )
     {history.push("/");window.location.reload(true)}
    else if (
        !history.location.pathname.startsWith("/user/home") &&
        !history.location.pathname.startsWith("/user/account") &&
        !history.location.pathname.startsWith("/user/media") &&
        !history.location.pathname.startsWith("/user/profile") &&
        !history.location.pathname.startsWith("/user/notes") &&
        !history.location.pathname.startsWith("/user/contact")&&
        !history.location.pathname.startsWith("/user/myobjective") &&
        !history.location.pathname.startsWith("/user/Todo") &&
        !history.location.pathname.startsWith("/user/privacy") &&
        !history.location.pathname.startsWith("/admin") &&
        !history.location.pathname.startsWith("/users") &&
        !history.location.pathname.startsWith("/meditate") &&
        !history.location.pathname.startsWith("/playlists") &&
        !history.location.pathname.startsWith("/user/:userId") &&
        !history.location.pathname.startsWith("/newUser") &&
        !history.location.pathname.startsWith("/newPlan") &&
        !history.location.pathname.startsWith("/newplaylist") &&
        !history.location.pathname.startsWith("/newsong") &&
        !history.location.pathname.startsWith("/newmeditate")&&
        !history.location.pathname.startsWith("/publications") &&
        !history.location.pathname.startsWith("/plans") &&
        !history.location.pathname.startsWith("/plan/:planId") &&
        !history.location.pathname.startsWith("/meditate/:meditateId") &&
        !history.location.pathname.startsWith("/playlist/:playlistId") &&
        !history.location.pathname.startsWith("/publication/:publicationId") &&
        !history.location.pathname.startsWith("/newpublication") &&
        !history.location.pathname.startsWith("/reports") &&
        !history.location.pathname.startsWith("/songs") &&
        !history.location.pathname.startsWith("/song/:songId") &&
        !history.location.pathname.startsWith("/messages")
      ) {
        history.push("/");
      }
   
 
   
  }, []);
  const [isAuth,setisAuth]=useState(false)
  return (
<div>
<Router>
  
      
      <div className="container">
        <Switch>
        <Route exact path='/' component={() => <SignInSide setAuth={()=>setisAuth(true)} /> } />
        <Route exact path='/registre' component={() => <Registration />} />
        <Route exact path='/resetpassword' component={() => <ForgetPassword />} />
        <Route exact path="/user/home"  component={() => <><Navbar /><Content /></>}/>
        <Route exact path="/user/account"  component={() => <><Navbar /><Account /></>}/>
        <Route exact path="/user/media" component={() => <><Navbar /><Media/></>}/>
        <Route exact path="/user/profile" component={() => <><Navbar /><Profile/></>}/>
        <Route exact path="/user/notes"component={() => <><Navbar /><Notes/></>}/>
        <Route exact path="/user/contact" component={() =><><Navbar /> <Contact/></>}/>
        <Route exact path="/user/myobjective" component={() =><><Navbar /> <EditObjective/></>}/>
        <Route exact path="/user/Todo" component={() => <><Navbar /><TodoList/></>}/>
        <Route exact path="/user/privacy" component={() => <><Navbar /><Privacy/></>}/>
        
        <Route path="/admin"  component={() => <><Topbar /><Sidebar /><Home /></>}   />
        <Route exact path="/users"component={() => <><Topbar /><Sidebar /><UserList /></>}/>
        <Route exact path="/meditate"component={() => <><Topbar /><Sidebar /><MeditateList /></>}/>
        <Route exact path="/playlists"component={() => <><Topbar /><Sidebar /><PlayList_list /></>}/>
        <Route exact path="/user/:userId"><Topbar /><Sidebar />  <User /></Route>
        <Route exact path="/newUser"><Topbar /><Sidebar /><NewUser /></Route>
        <Route exact path="/newPlan"><Topbar /><Sidebar /><NewPlan /></Route>
        <Route exact path="/newplaylist"><Topbar /><Sidebar />< NewPlayList/></Route>
        <Route exact path="/newsong"><Topbar /><Sidebar /><NewSong /></Route>
        <Route exact path="/newmeditate"><Topbar /><Sidebar /><NewMeditate /></Route>
        <Route exact path="/publications"><Topbar /><Sidebar /><PublicationsList /></Route>
        <Route exact path="/plans"><Topbar /><Sidebar /><PlansList /></Route>
        <Route exact path="/plan/:planId"><Topbar /><Sidebar /><Plan /></Route>
        <Route exact path="/meditate/:meditateId"><Topbar /><Sidebar /><Meditation /></Route>
        <Route exact path="/playlist/:playlistId"><Topbar /><Sidebar /><EditPlaylist /></Route>
        <Route exact path="/publication/:publicationId"><Topbar /><Sidebar /><Publication /></Route>
        <Route exact path="/newpublication"><Topbar /><Sidebar /><NewPublication /></Route>
        <Route exact path="/messages"><Topbar /><Sidebar /><MessagesList /></Route>
        <Route exact path="/reports"><Topbar /><Sidebar /><ReportsList /></Route>
        <Route exact path="/songs"><Topbar /><Sidebar /><SongsList /></Route>
        <Route exact path="/song/:songId"><Topbar /><Sidebar />  <Song /></Route>
        <Route exact path="/newpassword/:userId"><NewPassword /></Route>
        <Footer/>
        </Switch>
        

      </div>
    </Router>
    </div>
  );
}

export default Routing;

